import React, { useEffect, useState, useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import TextFieldUI from './UI/textFieldUI';
import * as actions from '../store/actions/index';

const { kakao } = window;

const KakaoMap = ({ history }) => {
  const [isMapSupported, setIsMapSupported] = useState(true);
  const [location, setLocation] = useState('');


  const dispatch = useDispatch();

  useEffect(() => {
    mapScript();
  }, [])

  const locationBtnHandler = useCallback((event) => {
    event.preventDefault();
    // set ÌïòÍ∏∞Ï†ÑÏóê, Ïûò Ï†ÅÏóàÎäîÏßÄ ÏúÑÏπò ÌïÑÌÑ∞ÎßÅ ÌïúÎ≤à Ìï¥ÏïºÌï®.
    const locationRegex = /^(ÏÑúÏö∏|Í≤ΩÍ∏∞ÎèÑ|Í∞ïÏõêÎèÑ|Ï∂©Ï≤≠Î∂ÅÎèÑ|Ï∂©Ï≤≠ÎÇ®ÎèÑ|Ï†ÑÎùºÎ∂ÅÎèÑ|Ï†ÑÎùºÎÇ®ÎèÑ|Í≤ΩÏÉÅÎ∂ÅÎèÑ|Í≤ΩÏÉÅÎÇ®ÎèÑ|Î∂ÄÏÇ∞|Ï†úÏ£º|ÏÑ∏Ï¢Ö|ÎåÄÍµ¨|Ïù∏Ï≤ú|Í¥ëÏ£º|ÎåÄÏ†Ñ|Ïö∏ÏÇ∞)/;

    if(!location.match(locationRegex))
        return alert('Ïò¨Î∞îÎ•∏ ÏßÄÏó≠ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');

    dispatch(actions.addLocation(location));
    history.push('/setting/5'); 
  }, [location]);

  const locationTextChangeHandler = useCallback((event) => {
    setLocation(event.target.value);
  }, []);

  const mapScript = () => {
      var container = document.getElementById('map'); 
      var options = {
        center: new kakao.maps.LatLng(37.624915253753194, 127.15122688059974),
        draggable: false,
        level: 5
      };

      var map = new kakao.maps.Map(container, options);
      if(navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(position => {
          var lat = position.coords.latitude, 
              lon = position.coords.longitude;

          var locPosition = new kakao.maps.LatLng(lat, lon), // ÎßàÏª§Í∞Ä ÌëúÏãúÎê† ÏúÑÏπòÎ•º geolocationÏúºÎ°ú ÏñªÏñ¥Ïò® Ï¢åÌëúÎ°ú ÏÉùÏÑ±Ìï©ÎãàÎã§
              message = '<div style="padding:5px;">Ïó¨Í∏∞Ïóê Í≥ÑÏã†Í∞ÄÏöî?!</div>'; // Ïù∏Ìè¨ÏúàÎèÑÏö∞Ïóê ÌëúÏãúÎê† ÎÇ¥Ïö©ÏûÖÎãàÎã§
          
          displayMarker(locPosition, message);
        });
      }else { 
          alert("Î∏åÎùºÏö∞Ï†ÄÏóêÏÑú GeolocationÏùÑ Ï†úÍ≥µÌïòÏßÄ ÏïäÏäµÎãàÎã§.")
          setIsMapSupported(false);
      }
    
    function displayMarker(locPosition, message) {
        var marker = new kakao.maps.Marker({  
            map: map, 
            position: locPosition
        }); 
        var iwContent = message, 
            iwRemoveable = true;
    
        // Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
        var infowindow = new kakao.maps.InfoWindow({
            content : iwContent,
            removable : iwRemoveable
        });
        
        // Ïù∏Ìè¨ÏúàÎèÑÏö∞Î•º ÎßàÏª§ÏúÑÏóê ÌëúÏãúÌï©ÎãàÎã§ 
        infowindow.open(map, marker);
        
        // ÏßÄÎèÑ Ï§ëÏã¨Ï¢åÌëúÎ•º Ï†ëÏÜçÏúÑÏπòÎ°ú Î≥ÄÍ≤ΩÌï©ÎãàÎã§
        map.setCenter(locPosition);      
    } 
  }

  return (
    <>
      {isMapSupported ? (
        <div id="map" style={{ width: 500, height: 500}}></div>
      ) : (
          <>
            <p style={{marginBottom: 0}} className="mr-5">ÎÇòÎäî</p>
            <TextFieldUI 
                width="50%"
                submitted={(e) => locationBtnHandler(e)} 
                changeHandler={(e) => locationTextChangeHandler(e)}
                label="ÏÇ¨ÎäîÍ≥≥" 
                placeholder="ÏÑúÏö∏ÌäπÎ≥ÑÏãú ÏÑ±Î∂ÅÍµ¨" 
            />
            <p className="text-lg ml-5">Ïóê ÏÇΩÎãàÎã§.</p>
          </>
        )}
    </>
  )
}
export default KakaoMap